body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 20px;
}

input {
  padding: 5px 10px;
  width: 200px;
}

.Output {
  padding: 20px;
}

.StepGrid {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Step--1,
.Step--2 {
  flex: 1 1;
}

.ButtonContainer {
  text-align: center;
}

button {
  background-color: black;
  color: white;
  text-align: center;
  padding: 10px 30px;
  border: none;
  font-size: 30px;
  margin-top: 20px;
  cursor: pointer;
}

button:hover {
  background-color: #666;
}

label {
  width: 100px;
  display: inline-block;
  text-align: right;
  margin-right: 14px;
}

